import { Field, InputType } from '@nestjs/graphql';
import { Type } from 'class-transformer';
import { IsEnum, IsOptional, IsString, IsUUID, ValidateNested } from 'class-validator';
import { BrandType } from '../../../../orm-config/entity/enum/brand-type';
import { PaymentMode } from '../../../../orm-config/entity/enum/payment-mode';
import { UpdatePaymentGatewayKeyInput } from './update-payment-gateway-key-input';
import { ColumnExists } from "../../../../validation";
import { Brand } from "../../../../orm-config/entity/payment/brand.entity";

@InputType()
export class UpdateBrandInput {
  
  @Field(() => String)
  @IsUUID(4)
  id: string;

  @Field(() => BrandType, { nullable: true })
  @IsEnum(BrandType)
  @IsOptional()
  brandType: BrandType;

  @Field(() => String, { nullable: true })
  @IsString()
  @IsOptional()
  @ColumnExists(Brand)
  name: string;

  @Field(() => PaymentMode, { nullable: true })
  @IsOptional()
  @IsEnum(PaymentMode)
  paymentMode: PaymentMode;

  @Field(() => [UpdatePaymentGatewayKeyInput], { nullable: true })
  @ValidateNested()
  @Type(() => UpdatePaymentGatewayKeyInput)
  @IsOptional()
  paymentGatewayKeys: UpdatePaymentGatewayKeyInput[];

}
